body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.floor {
    border: 1px solid black;
    box-sizing: border-box;
    flex-grow: 1;
    display: flex;
    align-items: center;
}
.elevatorSystem {
    display: flex;
}

.elevatorShaft{
    width: 30px;
    border: 1px solid black;
    position: relative;
    display: flex;
} 

.btnUp {
    color:grey;
}


.btnUp:hover {
    color: black;
}

.btnDown {
    color: grey;
}
.btnDown:hover {
    color: black;
}

.elevator {
    position: absolute;
    bottom: 0;
    background: rgba(123,123,123,.8);
    width: 100%;
}
.elevators {
    position: absolute;
    /* bottom: 0; */
    top: 0;
    left: 0;
    right: 0;
    background: rgba(123,123,123,.8);
    display: flex;
    justify-content: space-around;
}

.floorsBtns {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.floorsBtns .floorBtns {
    display: flex;
    box-sizing: border-box;
}

.floorsBtns .floorBtns button {
    border: none;
    text-align: center;
    padding: 0 2px;
    background: none;
    box-sizing: border-box;
    cursor: pointer;
}  
.building {
    min-height: 100vh; 
    min-width: 50vw;
    display: flex;     
}

.building > div {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow-y: auto;
    width: 100%;
    position: relative;   
}
* {
  box-sizing: border-box;
}
.App {
  display: flex;
  justify-content: center;
}
